.btn{
    padding:5px;
    font-weight: bold;
    color: white;
    border-radius: 3px;
    border: 1px solid #ccc;
    -webkit-box-shadow: 0 1px 1px #eee;
            box-shadow: 0 1px 1px #eee;
}

.red{
    background-color: red;
}

.blue{
    background-color: blue;
}
.green{
    background-color: #81C784;
}
.green:hover{
    opacity: 0.8;
}


