.formeach{
    text-align: center;
}
.formeach input{
    background-color: #fafafa;
    border:1px solid #ccc;
    font-size: 18px;
    margin-bottom: 7px;
    width:90%;
    font-family: 'Lato', sans-serif;
    padding:3px 5px;
}
.formeach textarea{
    background-color: #fafafa;
    border:1px solid #ccc;
    margin-bottom: 7px;
    height: 80px;
    width:90%;
    font-family: 'Lato', sans-serif;
    padding:3px 5px;
}
.formeach button{
    background-color: rgb(233, 74, 74);
    border:0px;
    width:90%;
    color: white;
    font-size: 18px;
    padding: 7px 2px;
    font-family: 'Roboto', sans-serif;

}
.title1{
    font-family: 'Lato', sans-serif;
}
.contactCont{
    margin-top:10px;
    height: 450px;
    padding: 10px;
    width: 100%;
}
.preferCont{
    display:-ms-flexbox;
    display:flex;
    width:90%;
    margin:0px auto;
    margin-bottom: 5px;
    -ms-flex-align:center;
        align-items:center;
}
.preferSelect{
    display:-ms-flexbox;
    display:flex;
    width:50%;
}
.preferSelect p{
    width:40%;
    
}
.borderDesign{
    border: 1px solid #ccc;
}
.aboutCont p{
    padding: 0px 8px;
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
}
.contactCont h2{
    margin-top:-5px;
    width: 100%;
    border-bottom: 1px solid #ccc;

    font-family: 'PT Sans', sans-serif;
}
@media (max-width:500px){
    .contactCont{
        height: 600px;
       
    }
}


