.content2{
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
.content2 h2{ 
    font-size: 30px;
    font-family: 'PT Sans', sans-serif;
    text-transform: capitalize;
}
.content2 a{ 
    margin:20px;
    font-size: 30px;
    text-decoration: none;
}
.content2 a:hover{
    text-decoration: underline;
}
.sponsorCont{
    width:80%;
    margin: 0 auto;
}
.sponsorCont img{
        
    width:100%;
    float: left;
}
.sponsorCont img:nth-child(n+4){
    height:60px;    
    float: left;
}
.listCont{
    width:1084px;
    margin: 10px auto;
}
.paraCont{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.paraCont article{
    text-align: left;
    width: 28%;
    padding: 0 30px;
    border-radius: 10px;
}
.paraCont article p:nth-child(2){
    color:#C06200;
    font-weight: bold;
  
}
.paraCont article:nth-child(2){
    margin-top:60px;
    border:2px solid #122632;
}
.paraCont article:nth-child(1){
    border:2px solid orange;
    height:250px;
}
.contactForm{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width:50%;
    margin:10px auto;
}
.contactForm div{
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}
.contactForm div label{
    display: -ms-flexbox;
    display: flex;
    width: 40%;
}
.contactForm div input{
    width:40%;
}
@media (max-width:500px){
    .paraCont{
    -ms-flex-direction:column;
        flex-direction:column;
}
.paraCont article{
    width: 60%;
    margin: 5px auto;
}
.listCont{
    width:90%;
    margin: 0px auto;
}
.sep{
    height:70px;
}
.paraCont article:nth-child(1){
    height:280px;
}
}


