.formeach{
    text-align: center;
    margin-top:10px;
}
.formeach input{
    background-color: #fafafa;
    border:1px solid #ccc;
    font-size: 18px;
    margin-bottom: 7px;
    width:90%;
    font-family: 'Lato', sans-serif;
    padding:3px 5px;
}
.formeach textarea{
    background-color: #fafafa;
    border:1px solid #ccc;
    margin-bottom: 7px;
    height: 80px;
    width:90%;
    font-family: 'Lato', sans-serif;
    padding:3px 5px;
}
.editArea{
    height:400px;
    overflow-y: auto;
}


