.disCont{
    display: block;
    float: left;
    width:340px;
    height: 340px;
    margin-left:10px;
    margin-bottom: 5px;
    text-align: left;
    border:1px solid #eee;
}
.disCont:hover{
    -webkit-box-shadow: 0px 0px 3px 3px #ccc;
            box-shadow: 0px 0px 3px 3px #ccc;
}
.eImgCont{
    width:100%;
    background-color: #eee;
    height: 200px;
    text-align:center;
}
.eImgCont img{
    height: 100%;
}
.despCont h1{
    font-size: 16px;
    color: black;
       font-weight: bold;
    font-family: 'PT Sans', sans-serif;
}
.disCont a{
   text-decoration:none;
}
.despCont p{
    margin-top:-5px;
    font-size: 15px;
    
    font-family: 'PT Sans', sans-serif;
    font-weight: 100;
    color:rgb(85, 84, 84);
}
.devCont{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.devCont div{
    width: 48%;
}
.devCont div img{
    width: 100%;
}
.despCont{
    padding:0 5px;
}

@media (max-width:500px){
    .disCont{
    width:100%;
   
}
}


