.mainNav{
    padding:10px 5px;
}

.mainNav a{
    color:#212121;
    font-weight: bold;
    font-family: 'Roboto', sans-serif;
    text-decoration: none;
}
.mainNav a:hover,
.mainNav a.active,
.mainNav a:active{
    padding:3px;
    color:black;    
    border-bottom: 2px solid #e64a19;
    -webkit-transition: ease-in 0.2s;
    -o-transition: ease-in 0.2s;
    transition: ease-in 0.2s;
}
@media (min-width:500px){
    .mainNav{
        padding:10px;
        margin-left: 10px;
    }
}



