.Label{
    font-weight: bold;
    display: block;
    margin-bottom: 8px;
}

.InputElement{
    border: 1px solid #ccc;
    padding:8px;
    width:95%;
    font-size: 18px;
    margin-top:10px;
}

.InputElement:focus{
   outline: #ccc;
   background-color: #FAFAFA;
   
}
.Invalid{
    background-color: #f9a8a8;
}

.ValidationError {
    font-family: 'PT Serif', serif;
    font-size: 12px;
    color: red;
    margin: 2px 0;
} 


