
.blogCont{
    margin:0px auto;
    width:80%;
}
.blogCont h1{
      font-family: 'PT Sans', sans-serif;
      width:100%;
      padding-bottom:10px;
      border-bottom:1px solid #ccc;
}
.BlogItem{
    width:100%;
    margin-top:10px;
}
.blogClass{
  width:33%;
  text-align:center;
  float:left;
  margin-right:8px;
  border:1px solid #eee;
  -webkit-box-shadow:1px 1px 3px 2px #ccc;
          box-shadow:1px 1px 3px 2px #ccc;
  background-color:white;
}
.blogClass:hover{
  cursor:pointer
}
.blogClass img{
  width:100%;
  height:250px;
}
.blogClass p{
    text-transform:capitalize;
    font-weight:bold;
    text-decoration:none;
}
.blogClass a{
    text-decoration:none;
}
@media (max-width:500px){
    .blogClass{
  width:100%;
  margin-bottom:10px;
}
}


