.mainCont{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
.tabCont{
    margin-bottom: 5px;
}
.tabCont li{
    display: block;
    float: left;
    margin-left: 10px;
    padding:8px;
}
.tabCont li:hover{
    border: 1px solid #ccc;
    background-color: #eee;
    cursor: pointer;
}
.activeTab{
    border: 1px solid #ccc;
    background-color: #eee;
}


