.bigCont{
    background-color:#eee;
}
.tab_title_list{
    margin-right:20px;
    background-color: white;
}
.tab_title_list li{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align:  center;
        align-items:  center;
    list-style-type: none;
    padding:5px 3px;
    margin-bottom:5px; 
    border-radius:2px;
    border-bottom-right-radius:25px;
    border-top-right-radius: 25px;
}
.tab_title_list li img{
    width:22px;
    height:22px;
    margin-right:5px;
}
.tab_title_list li:hover,
.activeTab{
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.8);
    -webkit-box-shadow: 0px 0px 1px 1px #ccc;
            box-shadow: 0px 0px 1px 1px #ccc;  
}




