.eCont{
    width:95%;
    margin-left: 10px;
    margin-bottom: 10px;
    background-color: white;
    height: 340px;
    -webkit-box-shadow: 1px 1px 1px #eee;
            box-shadow: 1px 1px 1px #eee;
    border:1px solid #ccc;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    float: left;
    display: block;
}
.despCont{
    padding:5px;
}
.despCont p:nth-child(1){
    margin-top: 0;
}
.despCont p{
    margin-top: -12px;
}
.despCont h1{
    margin-top: -15px;
}
.eImgCont{
    width:100%;
    background-color: blue;
    height: 230px;
    border-radius: 10px;
}
.eImgCont img{
    width:100%;
    height: 100%;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}
@media (min-width:700px){
    .eCont{
        width:380px;
        height: 340px;
    }
}


