.listUl{
    list-style-type: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    width:100%;
    z-index: 2;
    position: fixed;
    top: 40px;
    left:0px;
    background-color: white;
}
.itemcont{
    padding:10px 8px;

} 
.itemcont:hover{
    cursor: pointer;
  background-color: #eee;
    
}
.separator{
    height: 1px;
    background-color: #eee;
}

@media (min-width:500px){
    .listUl{
        position: static;
        margin-right: 240px;
        -ms-flex-direction: row;
            flex-direction: row;

    }
}
@media (min-width:720px){
    .listUl{
        margin-left: -250px;
    }
}


