.homeMain{
    width: 70%;
    margin:0px auto;
    background-color:white; 
    display: -ms-flexbox; 
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
.detailCont{
    padding:0px 20px;
    width: 100%;
}
.detailCont2{
    padding:7px 50px;
    width: 90%;
}
.errMsg{
    width:90%;
    border-radius: 5px;
    padding:8px;
    margin: 5px auto;
    color: white;
    font-family: 'PT Serif', serif;
    background-color:#FFCDD2;
    border:1px solid #db2323;
}
.Msg{
    width:90%;
    color: white;
    border-radius: 5px;
    padding:8px;
    margin: 5px auto;
    font-family: 'PT Serif', serif;
    background-color:#4CAF50;
    border:1px solid green;
}
.usercont button{
    padding:10px;
    font-size: 20px;
    
}
.usercont{
    margin-top:10px;
    text-align: center;
    width: 100%;
}
.voucherCont{
    border:1px solid rgb(153, 153, 153);
    border-radius:3px;
    margin-left:10px;
    padding:5px;
    width: 45%;
    float: left;
    margin-bottom: 7px;
}
.voucherCont button{
    background-color: #4CAF50;
    padding: 5px;
    color: white;
    border-radius:3px;
    border: 0px;
    font-weight: bold;
}
.voucherCont button:disabled{
    background-color: red;
    padding: 5px;
    color: white;
    border-radius:3px;
    border: 0px;
    font-weight: bold;
}
.voucherCont h4{
  margin-top:-3px;
}
.voucherCont p{
    margin-top:-12px;
  }
.titleVoucher{
    text-decoration: underline;
    font-weight: bold;
}
@media (max-width:500px){
    .homeMain{
        width: 100%;
       
    }
    .voucherCont{
        border:1px solid rgb(153, 153, 153);
        border-radius:3px;
        margin-left:10px;
        padding:5px;
        width: 90%;
        float: left;
        margin-bottom: 7px;
    }
    .detailCont{
        padding:0px 0px;
        width: 100%;
    }
}


