.hCont{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    height:56px;
    position: fixed;
    top:0;
    left:0;
    -ms-flex-align: center;
        align-items: center;
    background-color: white;
    width:100%;
    border-bottom: 1px solid #eee;
    -webkit-box-shadow: 0px 1px 1px #ccc;
            box-shadow: 0px 1px 1px #ccc;
    z-index: 100;
}
.desktopView{
    display:none;
 }
.mobileView{
     display:block;
    }
.BurgerIcon{
    border: 1px solid black;
    height:25px;
    width:25px;
    border-radius: 2px;
    padding:5px;
    text-align: center;
    margin-left: -70px;
}
.line{
    height: 1px;
    background-color: black;
    margin-bottom: 7px;
}
.line:nth-child(1){
    margin-top: 4px;
}
 @media(min-width:720px){
    .desktopView{
        display:block;
     }
     .mobileView{
     display:none;
   
    }
 }
@media (min-width:860px){
    .hCont{
        display:-ms-flexbox;
        display:flex;
        -ms-flex-pack: justify;
            justify-content: space-between;
        height:56px;
        position: fixed;
        top:0;
        left:0;
        -ms-flex-align: center;
            align-items: center;
        background-color: white;
        width:100%;
        padding-left: 90px;
        border-bottom: 1px solid #eee;
        -webkit-box-shadow: 0px 1px 1px #ccc;
                box-shadow: 0px 1px 1px #ccc;
        z-index: 100;
    }
   
}


