.disCont{
    display: block;
    float: left;
    width:340px;
    height: 370px;
    margin-bottom: 5px;
    margin-left: 10px;
    text-align: left;
}
.disCont:hover{
    -webkit-box-shadow: 0px 0px 3px 3px #ccc;
            box-shadow: 0px 0px 3px 3px #ccc;
}
.eImgCont{
    width:100%;
    background-color: #ccc;
    text-align: center;
    height: 200px;
}
.eImgCont img{
    height: 100%;
}
.despCont{
    padding:0px 5px;
}
.despCont h1{
    margin-top:-25px;
    font-size: 20px;
    color: black;
    font-family: 'PT Sans', sans-serif;
}
.despCont p{
    margin-top:-5px;
    font-size: 15px;
    font-family: 'PT Sans', sans-serif;
    font-weight: 100;
    color:rgb(85, 84, 84);
}
@media (max-width:500px){
    .disCont{
        width:100%;
        margin-left: 0;
    }
}



