.dropHead{
   display: block;
   float: left;
    margin-top: -5px;
    margin-left: 10px;
}
.dropHead2{
    -ms-flex-align: center;
        align-items: center;
    display:-ms-flexbox;
    display:flex;
    border-radius: 5px;
    color:#424242;
    padding: 15px;
    font-weight: bold;
    background-color: white;
}
.dropHead2:hover{
    cursor: pointer;
    background-color: #E0E0E0;
}
.aniArrow{
    font-size: 20px;
    margin-left: 5px;
    -webkit-transition: ease-out 0.5s;
    -o-transition: ease-out 0.5s;
    transition: ease-out 0.5s;

}
.aniArrow .active{
    color:red;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
.dropCont{
    display: block;
    position: absolute;
    background-color:white;
    min-width: 160px;
    -webkit-box-shadow: 0px 2px 3px 1px rgba(0,0,0,0.2);
            box-shadow: 0px 2px 3px 1px rgba(0,0,0,0.2);
    border:1px solid #BDBDBD;
    z-index: 100;
}



