.mainCont{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
.tabCont{
    margin-bottom: 5px;
    overflow-x: auto;
}
.tabCont li{
    display: -ms-flexbox;
    display: flex;
    float: left;
    margin-left: 10px;
    padding:8px;
    width:240px;
    -ms-flex-align: center;
        align-items: center;
}
.tabCont li img{
    width:70px;
    height: 70px;
    margin-right: 5px;
}
.tabCont li:hover{
    border: 1px solid #ccc;
    background-color: #eee;
    cursor: pointer;
}
.activeTab{
    border: 1px solid #ccc;
    background-color: #eee;
}
.panelCont ul{
    list-style-type: none;
    width: 1128px;
}
.panelCont ul li{
    display: -ms-flexbox;
    display: flex;
    border-bottom: 1px solid #eee;
    background-color: white;
    width:100%;
    padding:0 5px;
    
}
.panelCont ul li:nth-child(even){
    display: -ms-flexbox;
    display: flex;
    border-bottom: 1px solid #eee;
    background-color: #ccc;
}
.panelCont ul li p:nth-child(1){
    width:75px;
}
.panelCont ul li p:nth-child(2){
    width:80px;
}
.panelCont ul li p:nth-child(3){
    width:70px;
}
.panelCont ul li p:nth-child(4){
    width:200px;
}
.panelCont ul li p:nth-child(5){
    width:200px;
 }
 .panelCont ul li p:nth-child(6){
    width:130px;
 }
.panelCont ul li p{
    width: 24%;
}
.followuplink{
    font-size:12px;
}


