.content h1{
    font-size: 43px;
    color:white;
    font-family: 'PT Sans', sans-serif;
    text-transform: capitalize;
}
.content ul{
    list-style-type: none;
    margin-top: -10px;
}
.content ul li h2{
    margin-top:-10px;
  border-bottom: 1px solid black;
  opacity: 1;
}
.content ul li img{
   height:75px;
   width:75px;
}
.content ul li img:nth-child(1){
    height:75px;
    width:70px;
 }
.content ul li{
    float: left;
    width:28%;
    margin-left:15px;
    text-align: center;
    color: black;
    font-family: 'PT Sans', sans-serif;
    padding: 10px;
    border-radius: 5px;
    height:220px;
    opacity: .9;
}
.content ul li:nth-child(1){
    background-color: #D9E5D6;
    -webkit-animation-name: leftdown;
            animation-name: leftdown;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
}
.content ul li:nth-child(2){
    background-color: #EEE4E1;
    -webkit-animation-name: downup;
            animation-name: downup;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
}
.content ul li:nth-child(3){
    background-color: #EABA6B;
    -webkit-animation-name: rightdown;
            animation-name: rightdown;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
}
.content2{
    text-align: center;
}
.content2 h2{
    font-size: 43px;
    font-family: 'PT Sans', sans-serif;
    text-transform: capitalize;
}

@-webkit-keyframes leftdown{
   from{-webkit-transform: translate(-100px, -150px);transform: translate(-100px, -150px);
    opacity:0;}
   to{-webkit-transform: translate(0px, 0px);transform: translate(0px, 0px);
      opactiy:1;}
}

@keyframes leftdown{
   from{-webkit-transform: translate(-100px, -150px);transform: translate(-100px, -150px);
    opacity:0;}
   to{-webkit-transform: translate(0px, 0px);transform: translate(0px, 0px);
      opactiy:1;}
}

@-webkit-keyframes rightdown{
    from{-webkit-transform: translate(100px, -150px);transform: translate(100px, -150px);opacity:0;}
    to{-webkit-transform: translate(0px, 0px);transform: translate(0px, 0px);opacity:1;}
 }

@keyframes rightdown{
    from{-webkit-transform: translate(100px, -150px);transform: translate(100px, -150px);opacity:0;}
    to{-webkit-transform: translate(0px, 0px);transform: translate(0px, 0px);opacity:1;}
 }
 @-webkit-keyframes downup{
    from{-webkit-transform: translate(0, 150px);transform: translate(0, 150px);opacity:0;}
    to{-webkit-transform: translate(0px, 0px);transform: translate(0px, 0px);opacity:1;}
 }
 @keyframes downup{
    from{-webkit-transform: translate(0, 150px);transform: translate(0, 150px);opacity:0;}
    to{-webkit-transform: translate(0px, 0px);transform: translate(0px, 0px);opacity:1;}
 }
@media (max-width:500px){
    .content h1{
    font-size: 25px;
}
.content ul li{
    float: left;
    width:80%;
    margin-bottom:5px;
    height:180px;
    margin-left:0px;
}
}


