.proCont{
    width:47%;
    height:250px;
    margin-bottom: 4px;
    margin-right: 10px;
    display:block;
    float: left;
}
.proCont img{
    border-radius:10px;
    width:100%;
    margin-bottom: -15px;
}
.proCont2 p{
    margin-top: -10px;
}
@media(min-width:500px){
    .proCont{
        width:31%;
        height:275px;
    }
}
@media(min-width:800px){
    .proCont{
        width:32%;
        height:340px;
    }
}

@media(min-width:1080px){
    .proCont{
        width:250px;
        height:300px;
    }
}



