.hCont{
    width: 97%;
    margin: 5px auto;
    height: 370px;
}
.rentCont3{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    margin-top: 10px;
}
.rentCont3 a{
    display: none;
}
@media (min-width:500px){
    .hCont{
        height: calc((323px*2));
        overflow: hidden;
    }
}
@media (min-width:800px){
    .hCont{
        height: calc((373px*2));
        overflow: hidden;
    }
}
@media (min-width:960px){
    .hCont{
        height: calc((386px*2));
        overflow: hidden;
    }
}
@media(min-width:1080px){
    .rentCont3 a{
        display: block;
        margin-top: 30px;
        margin-right:75px;
    }
    .hCont{
        width: 1064px;
        margin: 5px auto;
        height: 370px;
        overflow: unset;
    }
}


