.outerCont{
    position: relative;
    height:65vh;
    text-align: center;
    z-index: 1;
}
.imgHolder{
    position: absolute;
    width:100%;
    height: 100%;
    background-size: cover; 
    background-position: center; 
    top:0;
    left:0;
    z-index: 2;
}
.imgHolder img{
    width: 100%;
  height:100%;
}
.overlay{
    position: absolute;
    z-index: 3;
    width:100%;
    height:100%;
    top:0;
    left:0;
    opacity: .2;
    background-color: #546E7A;
    
}
.content{
    position: absolute;
    z-index: 4;
    width:900px;
    top:0;
    left:240px;
    text-align: center;
}
@media (max-width:500px){
    .outerCont{
    height:90vh;
    z-index: 1;
}
.imgHolder img{
    height:100%;
  }
.content{
    z-index: 4;
    width:100%;
    top:2px;
        left:0px;
    text-align: center;
}
}




