.eImgCont{
    width:100%;
    height:70vh;
    text-align: center;
    background-color: #eee;
}
.eImgCont img{
    height:100%;
 
}
.bigCont{
    background-color:#fafafa;
}
.mainCont{
    background-color: white;
    -webkit-box-shadow: 0px 0px 6px 2px #ccc;
            box-shadow: 0px 0px 6px 2px #ccc;
    width: 78%;
    margin:0px auto;
}
.detailCont{
    padding:0 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-negative: 3;
      flex-shrink: 3;
}

.detailCont .proDet{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .titleCont h1{
      font-size: 21px;
      font-weight: bold;
    font-family: 'PT Sans', sans-serif;
   }
.titleCont p{
   margin-top:-15px;
   font-size: 16px;
   color:#616161;
   font-family: 'Lato', sans-serif;
  }
.titleCont{
    text-align: left;
    border-bottom: 1px solid #ccc;
}

.moredet{
    width: 90%;
   
}
.moredet h3{
    font-family: 'PT Sans', sans-serif;
}
.moredet article{
    padding:5px 10px;
    width: 100%;
    font-size: 14px;
    border-bottom: 1px solid #ccc;
}
.infoCont{
    width:100%;
    padding:5px;
}
.infoItem{
    width:50%;
    padding:4px 10px;
}

.infoItem span:nth-child(1){
    font-family: 'Lato', sans-serif;
    font-size: 14px;

}
.infoItem span:nth-child(2){
    font-weight: bold;
    color:#212121;
    font-size: 15px;
}
.freeCont article{
    font-size: 14px;
    color:#bf360c;
}
.freeCont h2{
    font-family: 'PT Sans', sans-serif;
}
.freeCont h2 a{
    font-size:12px;
    text-decoration: none;
    color:#c62828;
    font-family: 'Lato', sans-serif;
}
.freeCont article p{
    margin-top: -15px;
}
.pairCont{
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 5px;
    -ms-flex-direction: row;
        flex-direction: row;
    
    border-bottom: 1px solid #ccc;
}
.imageView{
    max-width:100vw;
    max-height:100vh;
    width:100vw;
    height:100vh;
   background-color: transparent;
}
.eImgCont2{
    width: 100%;
    text-align: center;
}
.eImgCont2 img{
   height: 100%;
}
.custom_overlay{
    padding:0px;
    overflow-y: hidden;
}
.clIcon{
    position: absolute;
    z-index: 1000;
    top: 14px;
    right:14px;
}
.isMobile{
        display:none;
    }
     .isDesktop{
                display:block;
    }
@media (max-width:500px){
    .isMobile{
        display:block;
    }
    .isDesktop{
                display:none;
    }
 .mainCont{
    width: 100%;

}
.eImgCont{
    height:40vh;
}
.detailCont{
  -ms-flex-direction:column;
      flex-direction:column;
}
}
@media (max-width:960px){
    .mainCont{
        width: 100%;
    
    }
}


